import { Injectable } from '@angular/core';
import { productos } from '../interface/productos.interface';
import { Observable , of } from 'rxjs';
import { pipe } from 'rxjs';
import { map, tap , catchError} from 'rxjs/operators';
import {HttpClient, HttpHeaders } from '@angular/common/http';
import { Url } from '../models/url';


@Injectable({
  providedIn: 'root'
})
export class Productos {

  public url:any;

  constructor(
    private _http: HttpClient
  ) {
    this.url = new Url;
   }


  validarToken():Observable<boolean>{
    const token = localStorage.getItem('token') || '';

    return this._http.get(`${this.url.url}auth/login/renew`,{
      headers: {
        'token': token
      }
    }).pipe(
      tap( (resp:any) => {

        localStorage.setItem('token', token);
    

      }),
      map(resp => true),
      catchError( error => of(false))
     
      )
      
    
  }

  getAllProduct():Observable<productos>{


    return this._http.get<productos>(this.url.url+'productos/listar');



  }


  PostProduct(producto:productos):Observable<productos>{

    const token = localStorage.getItem('token') || '';

   return this._http.post<productos>(this.url.url+'productos/guardar',producto,{
     headers:{
       'token': token
     }
   });

  }

  detalleProduct(id:String):Observable<any>{

    return this._http.get(this.url.url+'productos/'+id);

  }


  deleteProduct(id:String):Observable<any>{

    const token = localStorage.getItem('token') || '';


    return this._http.delete(this.url.url+'productos/borrar/'+id,{headers :{
      'token': token
    }
  });

  }


  
 editProduct(producto:productos):Observable<productos>{

    const token = localStorage.getItem('token') || '';

   return this._http.put<productos>(this.url.url+'productos/actualizar/'+producto.id,producto,{
     headers:{
       'token': token
     }
   });

  }



  // eliminar todos los productos pendientes

  eliminarPendiente():Observable<any>{

    return this._http.get(this.url.url+'eliminarPendientes');

  }




}
