import { Component, OnInit } from '@angular/core';
import { AdminModels } from '../models/admin';
import { ServiceAdmin } from '../service/login.service';
import { ServiceUsuarios } from '../service/usuarios.service';
import {Router} from '@angular/router';

import Swal from 'sweetalert2';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers: [ServiceAdmin]
})
export class LoginComponent {

  public loginAdmin:AdminModels ;
  public ResumenCorreo:any;
  public cargando:boolean;

  constructor(
    private _serviceAdmin: ServiceAdmin,
    private router:Router,
    private serviceUsuarios: ServiceUsuarios
  ) { 

    this.loginAdmin = new AdminModels('','','','','','','');
    this.cargando = false;
  }


 
  onSubmit(projectForm){

    this.cargando = true;

    this._serviceAdmin.loginAdmin(this.loginAdmin).subscribe( 

      resp => {

        this.cargando = false;
        this.router.navigateByUrl('/home');



        

      },
      err => {

         Swal.fire('Error',err.error.msg);
         this.cargando = false;

      }
    )




  }
 
 async recuperarPassword (){
   this.cargando = true;

    const { value: email } = await Swal.fire({
      title: 'Recuperar Contraseña',
      input: 'email',
      inputLabel: 'Ingrese su Correo',
      inputPlaceholder: 'Correo'
    })

    if (email) {


      this.serviceUsuarios.recuperarPassword(email).subscribe(
        (resp:any) => {

          this.cargando = false;
          Swal.fire(resp.msg)
        },
        err => {
          Swal.fire(err.error.msg)
        }
      )

     
    }
  }


}
