import { Component, OnInit } from '@angular/core';
import { Productos } from '../../service/productos.service';
import { ModelsProducto } from 'src/app/models/models';
import {Router} from '@angular/router';
import Swal from 'sweetalert2';
import { Url } from 'src/app/models/url';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';

@Component({
  selector: 'app-deteleproductos',
  templateUrl: './deteleproductos.component.html',
  styleUrls: ['./deteleproductos.component.css']
})
export class DeteleproductosComponent implements OnInit {

  public product : any;
  public detalleProduct: any;
  public url: any;
  public error:any;
  public statusEliminar: boolean;

  constructor(
    private _service:Productos,
    private route:Router
  ) { 

    this.product = new ModelsProducto('','','','','');
    this.url = new Url;
    this.statusEliminar = false;
  }

  ngOnInit(): void {


    this._service.getAllProduct().subscribe(
      resp => {

        if(resp){

          this.product = resp;

          this.detalleProduct = this.product.msg;

         console.log(this.product)

        }
      

      },
      error=> console.log(error)
    )
  }


  deseaEliminar(){

 
        
    if(this.statusEliminar == false){
               

      this.statusEliminar = true;
  
    }else{

      this.statusEliminar = false;
    }
   

  }

  eliminar(id:any){

    this._service.deleteProduct(id).subscribe(
      resp => {
        
        this.error = resp

        Swal.fire('Exito',this.error.msg)

        location.reload();

      }
      
     
      
      ,
      error=> {

        console.log(error.error.msg)
        Swal.fire('error',error.error.msg)
      }
    )



  }


  modificar(id:any){

    this.route.navigateByUrl('/editarProducto/'+id);
  }

}
