import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import {HttpClient, HttpHeaders } from '@angular/common/http';
import { tap } from 'rxjs/operators';
import { datoUsuario } from '../interface/productos.interface';
import { Url } from '../models/url';


@Injectable({
  providedIn: 'root'
})
export class ServiceUsuarios {

  public url:any;

  constructor(
    private _http: HttpClient
  ) { 

    this.url = new Url;

  }


  guardarUsuario(form:any){
    return this._http.post<any>(this.url.url+'admin/guardar',form);

  }


  datosUsuario(id:any){
    return this._http.get(this.url.url+'admin/detalle/'+id);
  }


  enviarResumen(form:any){

    return this._http.post(this.url.url+'correo', form);


  }


  recuperarPassword(email:String){


    return this._http.post(this.url.url+'admin/recuperarPassword',{correo:email});


  }



  Contact(form:any){

    return this._http.post(this.url.url+'correo/contact',form);
  }

 

}


