import { Component, OnInit, DoCheck} from '@angular/core';
import { Carrito } from '../service/carrito.service';
import {Router} from '@angular/router';


@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
  providers:[Carrito]
})
export class HeaderComponent implements OnInit , DoCheck {

  public cantidadCarro : any;
  public comprar: any;
  public muestraMenu: any;
  public mostrarLogin:String;

  constructor(

    private carro:Carrito,
    private route:Router
  

    
  ) { 

    this.muestraMenu = false;
    this.mostrarLogin = 'desconectado';
  

  }

  ngOnInit(){

    if(localStorage.getItem('token')){

      this.mostrarLogin = 'conectado';
    }
    
  }

  ngDoCheck(){


    if(localStorage.getItem('token')){

      this.mostrarLogin = 'conectado';
    }

    this.cantidadCarro = this.carro.cantidadCarrito();

  }

  vaciar(){

    this.carro.variarCarrito();

    this.route.navigateByUrl('/home');
  }


  verCarrito(){

    this.route.navigateByUrl('/pedido');
    this.carro.verCarro();
  }


  mostrarMenu(){


    if(this.muestraMenu == true){

      this.muestraMenu = false;

    }else{

      this.muestraMenu = true;
    }

  }


  login(){

    this.route.navigateByUrl('/login')

  }


  ocultarMenu(){
       
    this.muestraMenu = false;
    

  }

  


}
