import { Injectable , OnInit} from '@angular/core';
import { Observable , of } from 'rxjs';
import { pipe } from 'rxjs';
import { map, tap , catchError} from 'rxjs/operators';
import {HttpClient, HttpHeaders } from '@angular/common/http';
import { productos } from '../interface/productos.interface';

@Injectable({
  providedIn: 'root'
})
export class Carrito implements OnInit {



  constructor(

  ){


   }


   ngOnInit(){

   this.cantidadCarrito();

   }

  
   guardarProductos(producto){

    let productos:any;

     productos = this.ObtenerProductos();

     productos.push(producto);

     localStorage.setItem('productos', JSON.stringify(productos));

     this.cantidadCarrito();


    }


    
   ObtenerProductos(){

    let productosLS:any;

    if(localStorage.getItem('productos') === null){
      productosLS = [];
    }else{

      productosLS = JSON.parse(localStorage.getItem('productos'));

    }

    return productosLS;

   }


   cantidadCarrito(){

    if(JSON.parse(localStorage.getItem('productos')) === null){

      
    }else{

      let cantidad = [];

      for(let i = 0; i <= JSON.parse(localStorage.getItem('productos')).length; i++){
        cantidad.push(i);
      }

      let arr = cantidad.pop();

      let cantidadfinal = [];
      cantidadfinal.push(arr)

    return cantidadfinal;

    }


  }


  variarCarrito(){

    localStorage.removeItem('productos');


  }


  verCarro(){

    let localstorage = JSON.parse(localStorage.getItem('productos'));

    if(localstorage){

      return localstorage;

    }else{

     return localstorage = [];
    }
  }


  eliminar(index:any){

    let productos:any;

    productos = this.ObtenerProductos();

    productos.splice(index,1);

     localStorage.setItem('productos',JSON.stringify(productos));

    
  }


 
}
