import { NgModule } from '@angular/core';
import {Routes, RouterModule } from '@angular/router'; 
import { AuthGuard } from './guards/auth.guard';


import { LoginComponent } from './login/login.component';
import { AbautComponent } from './pages/abaut/abaut.component';
import { ContactComponent } from './pages/contact/contact.component';
import { DetalleProductoComponent } from './pages/detalle-producto/detalle-producto.component';
import { DeteleproductosComponent } from './pages/deteleproductos/deteleproductos.component';
import { EventComponent } from './pages/event/event.component';
import { HomeComponent } from './pages/home/home.component';
import { MenuComponent } from './pages/menu/menu.component';
import { RegistrarproductosComponent } from './pages/registrarproductos/registrarproductos.component';
import { PedidoComponent } from './pages/pedido/pedido.component';
import { DatosUsuarioComponent } from './pages/datos-usuario/datos-usuario.component';
import { DetallepedidoComponent } from './pages/detallepedido/detallepedido.component';
import { EstadopagoComponent } from './pages/estadopago/estadopago.component';
import { EditarProductoComponent } from './pages/editar-producto/editar-producto.component';
import { PedidosComponent } from './pages/pedidos/pedidos.component';
import { ComprasusersComponent } from './pages/comprasusers/comprasusers.component';


const routes: Routes = [
   
  {path:'', component: HomeComponent},
  {path:'login', component:LoginComponent},
  {path:'menu', component: MenuComponent},
  {path: 'eventos', component: EventComponent},
  {path: 'contacto', component: ContactComponent},
  {path: 'sobremi', component: AbautComponent},
  {path: 'detalleProducto/:id', component: DetalleProductoComponent},
  {path: 'pedido', component: PedidoComponent},
  {path: 'pedidos', component: PedidosComponent},
  {path: 'datos', component: DatosUsuarioComponent},
  {path: 'comprasusers/:id' , component: ComprasusersComponent},
  {path: 'estado-pago', component: EstadopagoComponent},
  {path: 'editarProducto/:id' , component: EditarProductoComponent },
  {path:'detallePedido/:id', component: DetallepedidoComponent},
  {path: 'registrarproductos', component: RegistrarproductosComponent,
  canActivate: [AuthGuard]},
  {path: 'vermisproductos' , component: DeteleproductosComponent},
  {path: '', redirectTo: '/', pathMatch: 'full'},
  {path:'**', component:  HomeComponent}
];


@NgModule({
  declarations: [],
  imports: [
      RouterModule.forRoot( routes)
  ],

  exports:[RouterModule]
})
export class AppRoutingModule { }
