
import { Component, OnInit , DoCheck} from '@angular/core';
import { Carrito } from 'src/app/service/carrito.service';
import { Router } from '@angular/router';
import { ServiceAdmin } from '../../service/login.service';

@Component({
  selector: 'app-pedido',
  templateUrl: './pedido.component.html',
  styleUrls: ['./pedido.component.css'],
  providers: [Carrito]
})
export class PedidoComponent implements OnInit , DoCheck{

  public pedido : any;
  public cantidad : any;
  public precioFinal:Number;
  public userActivo: Boolean;
  public idUser: any;
  public actualizarPedido;

  constructor(
    private carro : Carrito,
    private route : Router,
    private _serviceAdmin: ServiceAdmin
  ) {
    this.cantidad = [];
    this.pedido = [];
    this.userActivo = false;

   }


  ngOnInit(){

    this.calculo();
   

    this.logeado();

      
      }

      ngDoCheck(): void {
        
      this.calculo();

      }




      calculo(){

        this.actualizarPedido = this.carro.verCarro();

        this.pedido = this.actualizarPedido;
    
        let precio = [];
    
        let suma:any = 0;
    
          this.actualizarPedido.forEach(element => {
             
              precio.push(Number(element.precios));
          });
    
        for( let i = 0; i <= precio.length; i++){
    
          if(precio[i]){
    

            suma += precio[i];

            console.log(suma,'suma')
            
          }
    
        }
          
    return  this.precioFinal = suma.toFixed(3);

  
    
      }


      // eliminar productos
     
      eliminar(index){

       this.carro.eliminar(index);

       this.pedido = this.carro.verCarro();


      }


       // registrar usuario si no esta registrado 


      datos(){

        this.route.navigateByUrl('/datos');

      }




      logeado(){

        console.log(this.userActivo)

        this._serviceAdmin.validarTokenI().subscribe(
          (resp:any) => {
           if(resp){

            this.userActivo = true;

            //this.idUser = resp;

            let uid = resp;

          this.idUser = resp.uid; 

           }else{

            this.userActivo = false;
           }
          },
          error => {

            console.log(error)
          }
        )
      }


      resumenCompra(){

        this.route.navigateByUrl('/detallePedido/'+this.idUser);
      }




   
  }


  









