import { Component, OnInit } from '@angular/core';
import { Productos } from '../../service/productos.service';
import { productos } from '../../interface/productos.interface';
import { ModelsProducto } from '../../models/models';
import { Users } from 'src/app/models/users';
import { ServiceAdmin } from 'src/app/service/login.service';
import {Router} from '@angular/router';
import { Url } from 'src/app/models/url';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  providers:[Productos,ServiceAdmin]
})
export class HomeComponent implements OnInit {

  public product:any;
  public productDetalle:any;
  public url:any;
  public users: any;
  public statusUser:boolean;
  public rol: String;
  public text:String;
  public cargando:boolean;
  public detalleProductos:any;


  constructor(
    private _service:Productos,
    private _serviceAdmin: ServiceAdmin,
    private _router:Router
  ) {

   this.product = new ModelsProducto('','','','','');
   this.url = new Url;
   this.users = new Users('','','');
   this.statusUser = false;
   this.text ='1';
   this.cargando = false;
   this.detalleProductos= [];

   }

  ngOnInit(): void {

    /*  -----------------   */
    this.cargando = true;
  

    this._service.getAllProduct().subscribe((response:any) => {


      if(response){

        this.cargando = false;

        this.product = response;

        this.productDetalle = this.product.msg;

       
    

      }
      
    },
    error => {

      console.log(error)

    })


  

    
    this._serviceAdmin.validarTokenI().subscribe( resp => {

      if(resp){

        this.users = resp;

       this.rol = this.users.rol;

      }
    

    },error => console.log(error))


  }
  



  mostrarDatos(){

    localStorage.clear();

    location.reload();

  }


  registrarProductos(){

    this._router.navigateByUrl('/registrarproductos');

  }

  login(){

    this._router.navigateByUrl('/login');
  }


  vermisproductos(){

    this._router.navigateByUrl('/vermisproductos');
  }

  detalleProducto(id){

    this._router.navigateByUrl('/detalleProducto/'+id);

  }



  pedidos(){

    this._router.navigateByUrl('/pedidos');
   
  }

  verCompras(){

    this._router.navigateByUrl('/comprasusers/'+this.users.uid);

  }


}
