import { Component, OnInit } from '@angular/core';
import { datoUsuario } from 'src/app/interface/productos.interface';
import { ServiceUsuarios } from '../../service/usuarios.service'; 
import Swal from 'sweetalert2';
import { Router } from "@angular/router";

@Component({
  selector: 'app-datos-usuario',
  templateUrl: './datos-usuario.component.html',
  styleUrls: ['./datos-usuario.component.css']
})
export class DatosUsuarioComponent implements OnInit {

  public datos:any;
  public error:Boolean;
  public errorText:String;
  public cargando: boolean;

  constructor(
    private _serviceUsuarios: ServiceUsuarios,
    private _route: Router
  ) {

    this.datos = new datoUsuario('','','','','','','','',);
    this.error = true;
    this.cargando = false;

    
   }

  ngOnInit(): void {
  }


  OnSubmit(projectForm){

    this.cargando = true;

    if(this.datos.password != this.datos.password2){

      return this.error = false;

    }

    this._serviceUsuarios.guardarUsuario(this.datos).subscribe(
      
      resp => {


        if(resp){

          this.cargando = false;
          Swal.fire('Exito','Registrado');

          let id = resp.msg;


          
        this._route.navigateByUrl('/detallePedido/'+id);

        }
      }
      ,
    error => {

         this.errorText = error.error.msg.msg;

         if(this.errorText != error.error.msg.msg){

          this.errorText = error.error.msg.msg

         }

      }
    )
   
   
  }


}
