import { Component, OnInit } from '@angular/core';
import { pedidosService } from 'src/app/service/pedidos.service';
import { ServiceUsuarios } from 'src/app/service/usuarios.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-comprasusers',
  templateUrl: './comprasusers.component.html',
  styleUrls: ['./comprasusers.component.css']
})
export class ComprasusersComponent implements OnInit {

  public comprasRealizadas:any;
  public page:Number;

  constructor(
    private compras: pedidosService,
    private router : Router,
    private activateRouter: ActivatedRoute
  ) { }

  ngOnInit(): void {
   const id = this.activateRouter.snapshot.params;
   this.misCompras(id);
    

  }


  misCompras(id){

    this.compras.misCompras(id).subscribe( (resp:any) => {

     this.comprasRealizadas = resp.user;

     console.log(this.comprasRealizadas)

    },
    
    err => console.log(err))
    
  }




}
