import { Component, OnInit } from '@angular/core';
import { Correo } from 'src/app/models/correo';
import { ServiceUsuarios } from 'src/app/service/usuarios.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

  public datos:any;
  public cargando:boolean;
  constructor(
    private servicioContant: ServiceUsuarios
  ) { 

    this.datos = new Correo('','','','');
    this.cargando = false;
   }

  ngOnInit(): void {
  }



  OnSubmit(form){
this.cargando = true;
    this.servicioContant.Contact(this.datos).subscribe(
      resp => {

        if(resp){

          this.cargando = false;
        Swal.fire('Exito','Solicitud Enviada');

        form.reset();

        }


      },
      err => {

        console.log(err)

        Swal.fire('Error','No se pudo procesar su solicitud');

      }
    )


  }

}
