import { Component, OnInit } from '@angular/core';
import Swal from 'sweetalert2';
import { Productos } from '../../service/productos.service';
import { FileUploadService } from 'src/app/service/file-upload.service';
import { productos } from 'src/app/interface/productos.interface';
import {Router , ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-editar-producto',
  templateUrl: './editar-producto.component.html',
  styleUrls: ['./editar-producto.component.css']
})
export class EditarProductoComponent implements OnInit {

  public product : productos;
  public imagenUpload: File;
  public imagenUpload1: File;
  public imagenUpload2: File;
  public imagenUpload3: File;
  public status:String;
 public imageFalta:String;

  public disable:boolean;



  constructor(
    private _service : Productos,
    private fileupload : FileUploadService,
    private route:Router,
    private activateRoute : ActivatedRoute
     ) {

    this.disable = false;
    


  }

  ngOnInit(): void {

    const id = this.activateRoute.snapshot.params.id;

    this.getProducto(id);
  }

  getProducto(id){

    this._service.detalleProduct(id).subscribe( (resp:any) => {

      this.product = resp.detalle[0];

      console.log(this.product)

    },
    error => {

      console.log(error)
    })


  }

  onSubmit(form){

    console.log(this.product)
    this._service.editProduct(this.product).subscribe(resp => {

      if(resp){

        Swal.fire('Actualizado con exito')

      }

      this.fileupload.guardarFoto(this.imagenUpload,this.imagenUpload1,this.imagenUpload2,this.imagenUpload3, this.product.id).then(

        
       
        ).catch(error => console.log(error))


    }, error => {

      console.log(error)
      
      this.imageFalta = "Las Imagenes son obligatorias";

    })
      
  }

  


  
  

   



  CambiarImagen(file:File){

   this.imagenUpload = file;

 

  }


  CambiarImagen1(file:File){

    this.imagenUpload1 = file;

 
   }

   CambiarImagen2(file:File){


    this.imagenUpload2 = file;
 
   }

   CambiarImagen3(file:File){

    this.disable = true;

    this.imagenUpload3 = file;
 
   }

  }