import { Component, OnInit } from '@angular/core';
import { Productos } from '../../service/productos.service';
import {productos} from '../../interface/productos.interface';
import Swal from 'sweetalert2';
import { FileUploadService } from 'src/app/service/file-upload.service';
import { formatCurrency } from '@angular/common';
import { write } from '@popperjs/core';

@Component({
  selector: 'app-registrarproductos',
  templateUrl: './registrarproductos.component.html',
  styleUrls: ['./registrarproductos.component.css'],
  providers: [Productos, FileUploadService]
})
export class RegistrarproductosComponent implements OnInit {

  public product : productos;
  public title:string;
  public imagenUpload: any;
  public imagenUpload1: any;
  public imagenUpload2: any;
  public imagenUpload3: any;
  public status:String;
 public imageFalta:String;

  public disable:boolean;



  constructor(
    private _service : Productos,
    private fileupload : FileUploadService) {

    this.title = 'Registrar Productos';
    this.product = new productos('','','','','');
    this.disable = true;
    


  }

  ngOnInit(): void {


  }


  onSubmit(projectForm){


    if(this.disable){

      this._service.PostProduct(this.product).subscribe(response => {
  
        const id = response.id;
   
       this.fileupload.guardarFoto(this.imagenUpload,this.imagenUpload1,this.imagenUpload2,this.imagenUpload3, id).then(

        rep => console.log(rep)
         
       ).catch(error => console.log(error))
  
       this.status = "exito";

       if(this.status != "exito"){

        alert('Cargando...');

       }
       
       Swal.fire('Exito','Guardado con exito')
       projectForm.reset();

      },
      err=> {
        Swal.fire('Error',err.error.msg);
  
        this.status = "error";
      })
  
    }else{

      this.imageFalta = "Las Imagenes son obligatorias";

    }


    }

   



  CambiarImagen(file:File){

 
      this.imagenUpload = file;


  }


  CambiarImagen1(file:File){


  

      this.imagenUpload1 = file;
 

 
   }

   CambiarImagen2(file:File){

    

      this.imagenUpload2 = file;


 
   }

   CambiarImagen3(file:File){
  

      this.imagenUpload3 = file;


   }


  }





