import { Component, OnInit } from '@angular/core';
import {ActivatedRoute , Params } from '@angular/router';
import { Productos } from '../../service/productos.service';

import { Carrito } from 'src/app/service/carrito.service';
import { Url } from 'src/app/models/url';




@Component({
  selector: 'app-detalle-producto',
  templateUrl: './detalle-producto.component.html',
  styleUrls: ['./detalle-producto.component.css'],
  providers:[Carrito]
})
export class DetalleProductoComponent implements OnInit {

  public producto : any;
  public url:any;
  public detalle: any;
  public pedido : any;
  public extension0: any;
  public extension1: any;
  public extension2: any;
  public extension3: any;


  constructor(
    private _service: Productos,
    private _activateroute: ActivatedRoute,
    private _carrito: Carrito

  ) { 

    this.url = new Url;

  }

  ngOnInit(): void {

    

    const id = this._activateroute.snapshot.params.id;

    this._service.detalleProduct(id).subscribe(
      resp => {
        if(resp){

          this.detalle= resp;
          this.producto = this.detalle.detalle[0];

   

    
         this.extension0 = this.producto.img0.split('.');
         this.extension1 = this.producto.img1.split('.');
         this.extension2 = this.producto.img2.split('.');
         this.extension3 = this.producto.img3.split('.');

         console.log(this.extension0[3])

  

        }

        error => console.log(error);
      }

    );





  }


  guardarProductos(productos){

    this._carrito.guardarProductos(productos);


  }




}
