import { NgLocalization } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { TouchSequence } from 'selenium-webdriver';
import { pedidosService } from 'src/app/service/pedidos.service';
import { Productos } from '../../service/productos.service';
import Swal from 'sweetalert2';
import { ResourceLoader } from '@angular/compiler';

@Component({
  selector: 'app-pedidos',
  templateUrl: './pedidos.component.html',
  styleUrls: ['./pedidos.component.css']
})
export class PedidosComponent implements OnInit {

  public pedidos:any;
  public estado :String;
  public page:Number;
  public statusEliminar:boolean;

  constructor(
    private pedidoService:pedidosService,
    private productos:Productos
  ) { 
    this.estado = 'PENDIENTE';
    this.statusEliminar = false;
  }

  ngOnInit(): void {

    
    this.verPedidos();

  }



  verPedidos(){

    this.pedidoService.verPedidos().subscribe( (resp:any) => {

      this.pedidos = resp.pedidos;

      console.log(this.pedidos)


    },
    
    err => {

      console.log(err)

    })
    
  }


  cambiarEstado(id:String){
    const uid = {
      id
    }
    this.pedidoService.cambiarEstado(uid).subscribe( resp => {

      location.reload();

    },
    
    err => {
      console.log(err)
    })
  }


  EliminarPendientes(){

    this.productos.eliminarPendiente().subscribe( (resp:any) => {

      Swal.fire('Exito',resp.msg);

      location.reload();

    }), err => {

      console.log(err);
    }

  }


  eliminar(){

    this.statusEliminar = true;
  }

  cancelar(){

    this.statusEliminar = false;
  }
}
